import { deleteDoc, deleteField, updateDoc } from 'firebase/firestore';
import React, { FC, useEffect, useState } from 'react'
import { refChats, refUserChats } from '../firebase/refsDatabse';
import { useActions, useAppSelector } from '../hooks/redux';
import { Link, useNavigate } from 'react-router-dom';
import { RoutePathEnam } from '../types/routeType';
import { setDataToLoacalSorage } from '../utils/localstorage';


interface ChatHeaderProps {
    ChatImg: string,
    chatName: string,
    chatId: string,
    usersId: string[],
}

const ChatHeader: FC<ChatHeaderProps> = ({
    ChatImg,
    chatName,
    usersId,
    chatId,
}) => {
    const navigate = useNavigate()
    const [isShowDrop, setIsShowDrop] = useState<boolean>(false)
    const { setSelectedChat } = useActions()
    const deleteChat = async () => {
        await deleteDoc(refChats(chatId));
        usersId.map(async (userId) => {
            await updateDoc(refUserChats(userId), {
                [chatId]: deleteField()
            });
        })
        setSelectedChat({
            chatId: '',
            chatImg: '',
            chatName: '',
            usersId: [],
        })
        navigate(RoutePathEnam.chats)
    }
    return (
        <header className='bg-slate-200 border-b  items-center flex justify-between px-5 border-blue-300 h-[50px]'>
            <Link to={RoutePathEnam.chats}>
                <svg xmlns="http://www.w3.org/2000/svg" fill="#000000" height="45px" width="45px" version="1.1" id="Layer_1" viewBox="0 0 512 512">
                    <g>
                        <g>
                            <path d="M466.81,210.811H206.686v-89.279c0-19.911-16.198-36.109-36.109-36.109c-9.644,0-18.712,3.755-25.534,10.577    L10.577,230.467C3.756,237.288,0,246.356,0,256s3.756,18.712,10.577,25.534l134.467,134.468    c6.82,6.818,15.888,10.575,25.533,10.575c19.911,0,36.109-16.198,36.109-36.109v-89.279h260.126    C491.729,301.189,512,280.918,512,256S491.729,210.811,466.81,210.811z M466.81,280.791H196.487    c-5.633,0-10.199,4.567-10.199,10.199v99.478c0,8.663-7.048,15.711-15.711,15.711c-4.197,0-8.143-1.635-11.11-4.602L25,267.111    c-2.967-2.969-4.602-6.915-4.602-11.111c0-4.196,1.635-8.142,4.602-11.109l134.466-134.466c2.968-2.968,6.914-4.603,11.11-4.603    c8.663,0,15.711,7.048,15.711,15.711v99.478c0,5.632,4.566,10.199,10.199,10.199h270.325c13.669,0,24.79,11.121,24.79,24.79    C491.602,269.669,480.48,280.79,466.81,280.791z" />
                        </g>
                    </g>
                    <g>
                        <g>
                            <path d="M131.87,162.094c-3.984-3.982-10.442-3.982-14.425,0l-2.266,2.266c-3.983,3.984-3.983,10.442,0,14.425    c1.991,1.991,4.602,2.987,7.212,2.987s5.221-0.996,7.213-2.987l2.266-2.266C135.852,172.535,135.852,166.077,131.87,162.094z" />
                        </g>
                    </g>
                    <g>
                        <g>
                            <path d="M106.656,187.474c-3.984-3.981-10.441-3.982-14.425,0L32.565,247.14c-3.983,3.984-3.983,10.442,0,14.425    c1.992,1.991,4.601,2.987,7.212,2.987c2.61,0,5.221-0.997,7.213-2.987l59.666-59.666    C110.639,197.915,110.639,191.456,106.656,187.474z" />
                        </g>
                    </g>
                </svg>
            </Link>
            <div className='flex cursor-pointer gap-1 items-center'>
                <img className='h-[40px] w-[40px] rounded-full' src={ChatImg} alt="photoUser" />
                <div className='font-bold'>
                    {chatName}
                </div>
            </div>
            <div onClick={() => setIsShowDrop(prev => !prev)} className="dropdown">
                <button className="dropbtn">
                    <span></span>
                    <span></span>
                    <span></span>
                </button>
            </div>
            {
                isShowDrop && <div className='absolute p-1 bg-white shadow-md rounded-md right-[5px] top-[49px]'>
                    <button onClick={deleteChat} className='px-2 py-1'>delete</button>
                </div>
            }
        </header>
    )
}

export default ChatHeader